/*******************************************************************************
 * Styles for the base elements of the theme.
 ******************************************************************************/

// Typography

p, h1, h2, h3, h4, h5, h6, em, div, span, strong {
  color: var(--global-text-color);
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol"; 
}

a, table.table a {
  color: var(--global-text-color);
  &:hover {
    color: var(--global-hover-color);
    text-decoration: underline;
  }
  &:hover:after {
    //width: 100%;
  }
}

h2 {
  font-weight: 500;
}

p {
  font-size: 13pt;
  color: var(--global-muted);
}



// Math

.equation {
  margin-bottom: 1rem;
  text-align: center;
}

// Caption

.caption {
  font-size: 0.875rem;
  margin-top: 0.75rem;
  margin-bottom: 1.5rem;
  text-align: center;
}


// Profile

.profile {
  margin-left: 1rem;
  width: 100%;

  .address {
    margin-bottom: 5px;
    margin-top: 20px;
    font-family: monospace;
    font-size: 1.2rem;
    p {
      display: inline-block;
      margin: 0;
    }
  }
}

.profile-pic {
  margin-left: auto;
  margin-right: auto;
  float: none;
  text-align: center;
  img {
    width: 100px;
    height: 100px;
  }
  .address {
    margin-bottom: 5px;
    margin-top: 5px;
    font-family: monospace;
    font-size: 1rem;
    font-weight: normal;
    p {
      display: inline-block;
      margin: 0;
    }
  }
}

@media (min-width: 576px) {
  .profile {
    width: 30%;
    .address {
      p { display: block; }
    }
  }
}

.post-description {
  margin-bottom: 2rem;
  font-size: 0.875rem;
  a {
    color: inherit;
    &:hover {
      color: var(--global-theme-color);
      text-decoration: none;
    }
  }
}

.post {
  h2 {
    margin-bottom: 1rem;
  }
}

.author-blurb {
  font-weight: 400;
  margin-top: 1rem;
  opacity: 0.9;
}

.author-bio {
  max-width: 500px; // change width of bio section
  text-align: left;
  margin: 0 auto;
  p {
    color: var(--global-muted);
    font-size: 13pt;
  }
}


// Navbar customization

.navbar {
  box-shadow: none;
  border-bottom: 1px solid var(--header-border-color);
  opacity: 0.95;
  font-weight: 400;
  background-color: var(--global-bg-color);
}
.navbar.navbar-light {
  // Remove link decoration
  a {
    &:hover {
      text-decoration: none;
    }
  }
  .navbar-brand {
    color: var(--global-text-color);
  }
  .navbar-nav .nav-item .nav-link {
    color: var(--global-muted);
    font-family: 500;
    transition: 0.15s;
    &:hover {
      color: var(--global-hover-color);
    }
  }
  .navbar-nav .nav-item.active>.nav-link {
      background-color: var(--global-bg-color);
      &:hover {
        color: var(--global-hover-color);
      } 
  }
  


  .contact-icon {
    font-size: 2rem;
    a {
      &:hover {
        color: var(--global-hover-color);
      }
    }
  }
}

.navbar-toggler {
  .icon-bar {
    display: block;
    width: 22px;
    height: 2px;
    background-color: var(--global-icon-color);
    border-radius: 1px;
    margin-bottom: 4px;
    transition: all 0.2s;
  }
  .top-bar {
    transform: rotate(45deg);
    transform-origin: 10% 10%;
  }
  .middle-bar {
    opacity: 0;
  }
  .bottom-bar {
    transform: rotate(-45deg);
    transform-origin: 10% 90%;
  }
}

.navbar-toggler.collapsed {
  .top-bar {
    transform: rotate(0);
  }
  .middle-bar {
    opacity: 1;
  }
  .bottom-bar {
    transform: rotate(0);
  }
}


// News

.news table td {
  font-size: 1rem;
  color: var(--global-text-color);
}

.news table th {
  color: var(--global-text-color);
}

// Social (bottom)

.social {
  text-align: center;
  .contact-icon {
    font-size: 1rem;
    a {
      margin-left: 3px;
      margin-right: 3px;
      color: var(--global-icon-color);
      &:hover {
        color: var(--global-hover-color);
      }
    }
  }
  .contact-note {
    font-size: 0.8rem;
  }
}


// Footer
footer.fixed-bottom {
  background-color: var(--global-bg-color);
  opacity: 0.95;
  font-size: 0.75rem;
  .container {
    color: var(--global-muted); //var(--global-footer-text-color);
    padding-top: 9px;
    padding-bottom: 8px;
  }
  a {
    color: var(--global-text-color); //var(--global-footer-link-color);
    text-decoration: underline;
    &:hover {
      color: var(--global-hover-color);
      text-decoration: underline;
    }
  }
}

footer.sticky-bottom {
  border-top: 1px solid $grey-color-light;
  padding-top: 40px;
  padding-bottom: 40px;
  font-size: 0.9rem;
}

.bi-info-circle {
  color: var(--global-text-color); //var(--global-footer-text-color);
  &:hover {
    color: var(--global-hover-color);
    cursor: pointer;
  }
  &:active {
    -webkit-transform: scale(0.9, 0.9);
    transform: scale(0.9, 0.9);
  }
}


// Blog

.header-bar {
  border-bottom: 1px solid $grey-color-light;
  text-align: center;
  padding-top: 2rem;
  padding-bottom: 5rem;
  h1 {
    color: var(--global-theme-color);
    font-size: 5rem;
  }
}

.post-list {
  margin: 0;
  margin-bottom: 40px;
  padding: 0;
  li {
    border-bottom: 1px solid $grey-color-light;
    list-style: none;
    padding-top: 2rem;
    padding-bottom: 2rem;
    .post-meta {
      color: $grey-color;
      font-size: 0.875rem;
      margin-bottom: 0;
    }
    a {
      color: var(--global-text-color);
      text-decoration: none;
      &:hover {
        color: var(--global-theme-color);
      }
    }
  }
}

.pagination {
  .page-item {
    .page-link {
      color: var(--global-text-color);
      &:hover {
        color: $black-color;
      }
    }
    &.active .page-link {
      background-color: var(--global-theme-color);
      &:hover {
        background-color: var(--global-theme-color);
      }
    }
  }
}


// Distill

.distill {
  a:hover {
    border-bottom-color: var(--global-theme-color);
    text-decoration: none;
  }
}


// Projects

.projects {
  .grid-item {
    width: 250px;
    margin-bottom: 10px;
    a {
      color: black;
      text-decoration: none;
      &:hover {
        color: var(--global-theme-color);
      }
    }
  }
  .card img {
    width: 100%;
  }
}


// Publications

.publications {
  margin-top: 2rem;
  h1 {
    color: var(--global-theme-color);
    font-size: 2rem;
    text-align: center;
    margin-top: 1em;
    margin-bottom: 1em;
  }
  h2 {
    margin-bottom: 1rem;
    span {
      font-size: 1.5rem;
    }
  }
  h2.year {
    color: var(--global-text-color); //$grey-color-light;
    //border-bottom: 1px solid $grey-color; //$grey-color-light;
    //border-top: 1px solid $grey-color-light;
    padding-top: 1rem;
    margin-top: 2rem;
    margin-bottom: -1rem; //-2rem;
    text-align: left; //right;
    font-weight: 400;
  }
  ol.bibliography {
    list-style: none;
    padding: 0;
    margin-top: 0;

    li {
      margin-bottom: 1rem;
      .abbr {
        height: 2rem;
        margin-bottom: 0.5rem;
        abbr {
          display: inline-block;
          background-color: var(--global-theme-color);
          padding-left: 1rem;
          padding-right: 1rem;
          a {
            color: white;
            &:hover {
              text-decoration: none;
            }
          }
        }
        .award {
          color: var(--global-theme-color) !important;
          border: 1px solid var(--global-theme-color);
        }
      }
      .title {
        font-weight: bolder;
      }
      div.author, div.periodical, div.links {
        margin-left: 0.5rem;
      }
      .author {
        color: var(--global-muted);
        font-size: smaller;
        a {
          border-bottom: 1px dashed var(--global-theme-color);
          border-color: var(--global-hover-color);
          &:hover {
              //border-bottom-style: solid;
              text-decoration: none;
              border-color: var(--global-hover-color);
          }
        }

        a.pointer {
          border-bottom: 0px; 
          color: var(--global-muted);
          &:hover {
            cursor: default;
          }
        }
        
        > em {
          border-bottom: 1px solid;
          font-style: normal;
        }
      }
      .periodical {
        > em {
          color: var(--global-muted);
          font-size: smaller;
        }
      }
      .has-abstract {
        &:hover {
          color: var(--global-hover-color);
        }
      }
      .links {
        a.btn {
          color: var(--global-muted);
          border: 1px solid var(--global-muted);
          padding-left: 0.9rem;
          padding-right: 0.9rem;
          padding-top: 0.25rem;
          padding-bottom: 0.25rem;
          &:hover {
            color: var(--global-hover-color);
            border-color: var(--global-hover-color);
          }
          margin: 0;
          font-size:0.5rem;
          padding: 0.15rem 0.45rem;
        }
      }
      .hidden {
        font-size: smaller;
        max-height: 0px;
        overflow: hidden;
        text-align: justify;
        -webkit-transition: 0.15s ease;
        -moz-transition: 0.15s ease;
        -ms-transition: 0.15s ease;
        -o-transition: 0.15s ease;
        transition: all 0.15s ease;

        p {
          line-height: 1.4em;
          margin: 10px;
          font-size:10pt;
          color: var(--global-muted);
        }
        pre {
          font-size: 1em;
          line-height: 1.4em;
          padding: 10px;
        }
      }
      .hidden.open {
        max-height: 100em;
        -webkit-transition: 0.20s ease;
        -moz-transition: 0.20s ease;
        -ms-transition: 0.20s ease;
        -o-transition: 0.20s ease;
        transition: all 0.20s ease;
        font-size:smaller;
      }
      div.abstract.hidden {
        border: solid 1px var(--global-bg-color);
      }
      div.abstract.hidden.open {
        border-color: none; //var(--global-theme-color);
      }
    }
  }
}

// Rouge Color Customization
code {
  color: var(--global-theme-color);
}
// Transitioning Themes
html.transition,
html.transition *,
html.transition *:before,
html.transition *:after {
  transition: all 750ms !important;
  transition-delay: 0 !important;
}

// To Top Button
.back-to-top {
    cursor: pointer;
    position: fixed;
    bottom: 50px;
    right: 20px;
    width: 40px;
    height: 40px;
    padding: 6px 0px;
    border-radius: 30px;
    margin: 0 auto;
    text-align: center;
    font-size: 1.25rem;
    line-height: 1.42857;
    display: none;
    background-color: var(--global-theme-color) !important;
    &:hover {
      background-color: var(--global-hover-color) !important;
    }
}

// Dropdown

.navbar .dropdown-menu > .dropdown-item {
  padding-top: 0px;
  padding-bottom: 0px;
  padding-left: 15px;
  margin-top: -2.5px;
  margin-bottom: -2.5px;
  &:active {
    background-color: var(--global-bg-color);
  }
  color: var(--global-muted)!important;
}

.dropdown-header {
  padding-left: 10px;
  font-weight: 500;
  color: var(--global-text-color);
}

// Header Color
.header-name {
  font-weight: normal;
  font-size: 20px;
  &:hover {
    color: var(--global-hover-color);
  }
}

.header-last {
  font-weight: normal;
  font-size: 20px;
}

.header-first {
  font-weight: normal;
  font-size: 20px;
}

// .navbar-brand:hover > .header-last {
//   color: var(--global-hover-color);
// }



.navbar-nav li:hover>.dropdown-menu {
  display: block;
}

.dropdown-menu {
  border: 1px solid var(--header-border-color);
}

.navbar-nav > li > .dropdown-menu {
  background-color: var(--global-bg-color);
}
.navbar-nav .dropdown-menu a {
  color: var(--global-text-color) !important;
  &:hover {
    background-color: var(--global-dropdown-hover);
    color: var(--global-hover-color) !important;
  }
  &:active {
    background-color: var(--global-bg-color);
  }
}

